:root{--base-color-plus-5: #3F51B5;--base-color-plus-10: #3949AB;--base-color-plus-15: #303F9F}.flex-vertically{display:flex;flex-direction:column;align-items:center;justify-content:center}.flex-horizontally{display:flex;flex-direction:row;align-items:center;justify-content:center}.header{width:100%;margin:0;padding:0;border:none;background-color:#3F51B5;color:white;font-weight:700;line-height:3em;text-align:center;z-index:3}.header__link{padding:0.5rem;font-size:1.25rem;will-change:transform;transition:0.25s transform ease-in-out}.header__link:link,.header__link:visited{color:white}.header__link:hover,.header__link:focus,.header__link:active{color:#FFD54F;transform:scale(1.125)}@media only screen and (min-width: 425px){.header__link{font-size:1.375rem}}@media only screen and (min-width: 768px){.header__link{font-size:1.5rem}}.header__nav{display:flex;width:100%;min-height:56px;text-align:center;background-color:#3F51B5;color:white}.header__nav{position:sticky;-webkit-position:sticky;top:0;z-index:3}.header__nav .nav__link{flex:1;color:white;font-size:1rem;will-change:transform;transition:0.25s transform ease-in-out;line-height:56px}.header__nav .nav__link:link,.header__nav .nav__link:visited{color:white}.header__nav .nav__link.active{background-color:rgba(0,0,0,0.125)}.header__nav .nav__link:hover,.header__nav .nav__link:focus,.header__nav .nav__link:active{color:#FFD54F;transform:scale(1.1);text-decoration:underline}@media only screen and (min-width: 425px){.header__nav .nav__link{font-size:1.125rem}}@media only screen and (min-width: 768px){.header__nav .nav__link{font-size:1.25rem}}
