footer .container .contact-form{width:100%;max-width:480px;font-family:Arial, Helvetica, sans-serif;text-align:center;letter-spacing:1px}footer .container .contact-form label{margin:0.5rem 1rem;border-radius:0.25rem;display:flex;flex-direction:column;justify-content:center;font-size:1rem;text-align:left}footer .container .contact-form__control{min-height:1rem;padding:0.5rem;border-radius:0.125rem;border:1px solid black;font-size:0.875rem}footer .container .contact-form__control:focus{border:0.1rem  solid #E91E63;outline:0}footer .container .contact-form__control input[type='email']{text-transform:lowercase}footer .container .contact-form__control input[name='name'],footer .container .contact-form__control input[name='subject']{text-transform:capitalize}footer .container .contact-form__button{height:2rem;border:0;border-radius:0.125rem;font-size:0.875rem;background:rgba(0,0,0,0.6);color:white;text-transform:uppercase;letter-spacing:1px;font-weight:700}footer .container .contact-form__button:focus{border:0.1rem  solid #E91E63;outline:0}footer .container .contact-form__button:active{background:rgba(0,0,0,0.6)}
